				}
			},2000);
			setInterval(() => {
				//开始flag为true x=0
				if(this.flag && this.x<1){
					this.x=this.x+0.01;
				}else if(this.x-1>0 && this.x-1<0.01){
					this.flag=false;
					this.x=this.x-0.01;
				}else if(!this.flag && this.x>0.01){
					this.x=this.x-0.01;
				}else if(this.x>0&&this.x<0.01){
					this.flag=true;
					this.x=this.x+0.01;
				}
			},30);
